body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.container {
  background-color: #add8e6;
  margin: 0 auto;
  padding: 20px;
}

.search-form {
  justify-content: center;
  margin-top: 20px;
  display: flex;
}

input[type="text"] {
  padding: 10px;
  font-size: 16px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #00f;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

button:hover {
  color: green;
  background-color: plum;
}

.gallery {
  background-color: #d3d3d3;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 20px;
  display: flex;
}

.photo-card {
  text-align: center;
  margin: 10px;
}

.photo-card img {
  width: 100%;
  max-width: 300px;
  height: auto;
  border-radius: 5px;
}

.info {
  background-color: #f2f2f2;
  border-radius: 5px;
  padding: 5px;
}

.info-item {
  margin: 5px 0;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
  display: none;
}

